<template>
  <div class = "preparing">
    <ProgressBar :step="0"/>
    <!-- instruction text -->
    <div class="txt">
        <h1>Please Check Your Microphone</h1>
        <div class="instruction">
            <p>You can check your microphone by recording for a few seconds, and listen to it, or record again if the recording quality does not meet expectations.</p>
            <p>Once you think that your microphone works as expected, you can click "Next step" button to enter the recording phase.</p>
        </div>
    </div>
    <!-- buttons -->
    <div v-show="record">
        <div class="record-btn" v-show="!isRecording">
            <el-button icon="el-icon-microphone" circle @click="startRecording"></el-button>
            <div class="record-txt"> Click and Speak</div>
        </div>
        <div class="record-btn" v-show="isRecording">
            <el-button circle @click="stopRecording"><img src="./assets/stop.svg"></el-button>
            <div class="record-txt"> Stop recording</div>
        </div>
    </div>

    <div class="replay-btns" v-show="!record">
        <div> 
            <audio controls="controls" controlsList="nodownload" :src="audio_source"></audio>
        </div>
        
        <!-- <div class="play" v-show="!isPlaying">
            <el-button icon="el-icon-headset" circle @click="playing"></el-button>
            <div class="txt"> Play</div>
        </div>
        <div class="play" v-show="isPlaying">
            <el-button icon="el-icon-video-pause" circle @click="pausePlaying"></el-button>
            <div class="txt">Pause playing</div>
        </div> -->
        <div class="retake">
            <el-button icon="el-icon-refresh-left" circle @click="startRecording"></el-button>
            <div class="txt"> Record Again</div>
        </div>
        
    </div>
    <!-- next button -->
    <div class="next-btn"><el-button type="primary" round @click="toRecordingPhase">Next Step</el-button></div>
    
  </div>
</template>

<script>
import ProgressBar from "./components/ProgressBar.vue"
import WavEncoder from './utils/wav-encoder'
// import { convertTimeMMSS } from './utils/utils.js'

// var recorder = null


export default {
    name: "preparing",
    components:{
        ProgressBar,
    },
    data(){
        return{
            record: true,
            isRecording: false,
            isPlaying: false,
            context: null,
            source: null,
            processor: null,
            stream: null,
            samples: [],
            record_file: null,
        }
    },
    // mounted(){
    //     // recorder = new Recorder({
    //     //     sampleBits: 16,                 // 采样位数，支持 8 或 16，默认是16
    //     //     sampleRate: 16000,              // 采样率，支持 11025、16000、22050、24000、44100、48000，根据浏览器默认值，我的chrome是48000
    //     //     numChannels: 1,                 // 声道，支持 1 或 2， 默认是1
    //     //     // compiling: false,(0.x版本中生效,1.x增加中)  // 是否边录边转换，默认是false
    //     // })
    // },
    computed:{
        audio_source: function(){
            return this.record_file==null?"":this.record_file.url
        } 
    },
    methods:{
        // callback (data) {
        //     console.debug(data)
        // },
        
        startRecording: function(){
            this.record = true
            this.isRecording = true
            navigator.mediaDevices.getUserMedia({ audio: true, video: false })
            .then((stream)=>{
                this.context = new AudioContext()
                this.source = this.context.createMediaStreamSource(stream);
                this.processor = this.context.createScriptProcessor(1024, 1, 1);
                this.stream = stream
                this.source.connect(this.processor);
                this.processor.connect(this.context.destination);
                var vm = this
                this.processor.addEventListener('audioprocess', function(e) {
                    const sample = e.inputBuffer.getChannelData(0)
                    // console.log("this:",vm)
                    vm.samples.push(new Float32Array(sample))
                })
            })
            console.log("start recording")
            // get permission
            // Recorder.getPermission().then(() => {
            //     console.log('Got microphone permission');
            // }, (error) => {
            //     console.log(`${error.name} : ${error.message}`);
            // });

            this.isRecording = true
            // recorder.start().then(()=>{
            //     setTimeout(()=>{
            //         // recording_data = recorder.getWAVBlob();
            //         recorder.stop()
            //     }, 10000) //10s
            //     console.log("Start recording")
            // })
            
        },
        stopRecording: function(){
            this.stream.getTracks().forEach((track) => track.stop())
            this.processor.disconnect()
            this.source.disconnect()
            this.context.close()
            // convert it to WAV   
            let wavEncoder = new WavEncoder({
                bufferSize : 1024,
                sampleRate : 44100,
                samples    : this.samples
            })
            this.record_file = wavEncoder.finish()
            this.samples = []
            this.isRecording = false
            this.record = false
            console.log("stop recording")
            console.log(this)
        },
        playing: function(){
            // recorder.play();
            this.$refs.audio.play()
            this.isPlaying = true
            console.log("Playing")

        },
        pausePlaying: function(){
            this.isPlaying = false
            // recorder.pausePlay();
        },
        toRecordingPhase: function() {
            this.$router.push({
                name: "recording"
            })
        }
    },
    // beforeDestroy(){
    //     recorder.destroy().then(function() {
    //         recorder = null;
    //     });
    // }
}


</script>

<style scoped>
    .preparing{
        width: 100%;
    }
    .txt{
        /* width: 80%;
        margin-left: 10%; */
        margin-top: 5%;
        text-align: center;
    }
    .instruction{
        width: 60%;
        margin-left: 20%;
        margin-top: 3%;
        text-align: left;
    }
    .record-btn{
        /* position: absolute; */
        /* margin-left: 50%; */
        margin-top: 4%;
    }
    .record-btn .el-button{
        width: 8rem;
        height: 8rem;
        /* margin-left: -4rem; */
        font-size: xx-large;
    }
    .record-txt{
        width: auto;
        /* margin-left: -4rem; */
        overflow: visible;
        text-align: center;
        margin-top: 2%;
    }
    .replay-btns{
        display: flex;
        margin-top: 4%;
        margin-left: 50%;
    }
    .play{
        float: inline-start;
        margin-left: -5rem;
        text-align: center;
    }
    .retake{
        margin-left: 2rem;
        text-align: center;
    }
    .replay-btns .el-button{
        width: 4rem;
        height: 4rem;
        font-size: x-large;
    }
    .replay-btns .txt{
        margin-top: 8%;
    }
    .next-btn{
        margin-left: 70%;
    }
</style>