<template>
  <div class="recording">
    <ProgressBar :step="step" />
    <div class="intro" v-show="!start">
      <h1>
        After <span v-bind:class="{ emph: bolder }">{{ sec }}</span> seconds,
        please speak words in category
        <span style="color: #0288d1">{{ category }}</span
        >.
      </h1>
    </div>

    <div class="recording" v-show="start" v-bind:class="{ load: loading }">
      <h1>
        Please speak words in category
        <span style="color: #0288d1">{{ category }}</span
        >.
      </h1>
      <canvas class="wave" ref="audioCanvas"></canvas>
    </div>

    <div class="loader" v-show="loading">
      <div class="l_main">
        <div class="l_square"><span></span><span></span><span></span></div>
        <div class="l_square"><span></span><span></span><span></span></div>
        <div class="l_square"><span></span><span></span><span></span></div>
        <div class="l_square"><span></span><span></span><span></span></div>
        <div class="load-txt">
          <h3>We're now uploading your recording file, please wait...</h3>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ProgressBar from "./components/ProgressBar.vue";
import WavEncoder from "./utils/wav-encoder";
// var vm = this

export default {
  name: "recording",
  data() {
    return {
      step: 1,
      start: false,
      bolder: false,
      sec: 10,
      category: "Animals",
      context: null,
      source: null,
      processor: null,
      stream: null,
      samples: [],
      record_file: null,
      loading: false,
    };
  },
  components: {
    ProgressBar,
  },
  mounted() {
    this.timer = setInterval(() => {
      if (this.sec == 0) {
        clearInterval(this.timer);
        this.startRecording();
      } else if (this.sec == 4) {
        this.bolder = true;
      }
      this.sec--;
    }, 1000); // 1s
  },
  beforeUnmount() {
    clearInterval(this.timer);
  },
  methods: {
    startRecording: function () {
      this.start = true;
      console.log("start recording");
      navigator.mediaDevices
        .getUserMedia({ audio: true, video: false })
        .then((stream) => {
          this.context = new AudioContext();
          this.source = this.context.createMediaStreamSource(stream);
          this.processor = this.context.createScriptProcessor(1024, 1, 1);
          this.stream = stream;
          this.source.connect(this.processor);
          this.processor.connect(this.context.destination);
          let vm = this;
          let g = this.$refs.audioCanvas.getContext("2d");
          let width = this.$refs.audioCanvas.width;
          let height = this.$refs.audioCanvas.height;
          g.strokeStyle = "#409EFF";
          g.translate(0.5, height / 2 + 0.5);
          this.processor.addEventListener("audioprocess", function (e) {
            const sample = e.inputBuffer.getChannelData(0);
            // let output = e.outputBuffer.getChannelData(0);
            vm.samples.push(new Float32Array(sample));
            g.clearRect(-0.5, -height / 2 - 0.5, width, height);
            g.beginPath();
            for (let i = 0; i < width; i++)
              g.lineTo(
                i,
                (height / 2) * sample[((sample.length * i) / width) | 0]
              );
            g.stroke();
          });
        });

      setTimeout(() => {
        this.stopRecording();
      }, 7000); // 70 seconds
    },
    stopRecording: function () {
      this.stream.getTracks().forEach((track) => track.stop());
      this.processor.disconnect();
      this.source.disconnect();
      this.context.close();
      // convert it to WAV
      let wavEncoder = new WavEncoder({
        bufferSize: 1024,
        sampleRate: 44100,
        samples: this.samples,
      });
      this.record_file = wavEncoder.finish();
      this.samples = [];
      this.isRecording = false;
      this.record = false;
      console.log("stop recording");
      console.log(this);
      this.step = 2
      this.loading = true;
      setTimeout(() => {
        this.$ajax
          .post("/upload", {
            "audioFile": this.record_file.blob,
          })
          .then((response) => {
            console.log(response)
            this.$router.push({
              name: "complete",
            });
          })
          .catch((error) => {
            console.log(error);
            this.errored = true;
          });
      }, 2000);
    },
  },
};
</script>

<style scoped>
.intro {
  margin-top: 5%;
  width: 100%;
  text-align: center;
}
.emph {
  color: #03a9f4;
}

.recording {
  width: 100%;
  margin-top: 5%;
  /* position: absolute; */
}

.wave {
  width: 50%;
}

.load {
  background: inherit;
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -ms-filter: blur(5px);
  -o-filter: blur(5px);
  filter: blur(10px);
  filter: progid:DXImageTransform.Microsoft.Blur(PixelRadius=4, MakeShadow=false);
  box-shadow: 0 1px 1px white;
}

.loader {
  height: 100%;
  width: 100%;
}
.loader .l_main {
  position: absolute;
  top: 45%;
  left: 50%;
  /* width: 172px; */
  width: fit-content;
  height: 128px;
  margin: 0;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.loader .load-txt{
    position: absolute;
    top: 130px;
    left: 50%;
    width: 500px;
    transform: translate(-50%, 0);
    /* width: 500px;
    min-width: fit-content; */
}

@media (max-width: 550px) {
  .loader {
    -webkit-transform: scale(0.75);
    transform: scale(0.75);
  }
}
@media (max-width: 440px) {
  .loader {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
}
.l_square {
  position: relative;
}
.l_square:nth-child(1) {
  margin-left: 0px;
}
.l_square:nth-child(2) {
  margin-left: 44px;
}
.l_square:nth-child(3) {
  margin-left: 88px;
}
.l_square:nth-child(4) {
  margin-left: 132px;
}
.l_square span {
  position: absolute;
  top: 0px;
  left: 20px;
  height: 36px;
  width: 36px;
  border-radius: 2px;
  background-color: #71a7b4;
}
.l_square span:nth-child(1) {
  top: 0px;
}
.l_square span:nth-child(2) {
  top: 44px;
}
.l_square span:nth-child(3) {
  top: 88px;
}
.l_square:nth-child(1) span {
  -webkit-animation: animsquare1 2s infinite ease-in;
  animation: animsquare1 2s infinite ease-in;
}
.l_square:nth-child(2) span {
  -webkit-animation: animsquare2 2s infinite ease-in;
  animation: animsquare2 2s infinite ease-in;
}
.l_square:nth-child(3) span {
  -webkit-animation: animsquare3 2s infinite ease-in;
  animation: animsquare3 2s infinite ease-in;
}
.l_square:nth-child(4) span {
  -webkit-animation: animsquare4 2s infinite ease-in;
  animation: animsquare4 2s infinite ease-in;
}
.l_square span:nth-child(1) {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}
.l_square span:nth-child(2) {
  -webkit-animation-delay: 0.15s;
  animation-delay: 0.15s;
}
.l_square span:nth-child(3) {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}
@-webkit-keyframes animsquare1 {
  0%,
  5%,
  95%,
  100% {
    -webkit-transform: translate(0px, 0px) rotate(0deg);
    transform: translate(0px, 0px) rotate(0deg);
  }
  30%,
  70% {
    -webkit-transform: translate(-40px, 0px) rotate(-90deg);
    transform: translate(-40px, 0px) rotate(-90deg);
  }
}
@keyframes animsquare1 {
  0%,
  5%,
  95%,
  100% {
    -webkit-transform: translate(0px, 0px) rotate(0deg);
    transform: translate(0px, 0px) rotate(0deg);
  }
  30%,
  70% {
    -webkit-transform: translate(-40px, 0px) rotate(-90deg);
    transform: translate(-40px, 0px) rotate(-90deg);
  }
}
@-webkit-keyframes animsquare2 {
  0%,
  10%,
  90%,
  100% {
    -webkit-transform: translate(0px, 0px) rotate(0deg);
    transform: translate(0px, 0px) rotate(0deg);
  }
  35%,
  65% {
    -webkit-transform: translate(-40px, 0px) rotate(-90deg);
    transform: translate(-40px, 0px) rotate(-90deg);
  }
}
@keyframes animsquare2 {
  0%,
  10%,
  90%,
  100% {
    -webkit-transform: translate(0px, 0px) rotate(0deg);
    transform: translate(0px, 0px) rotate(0deg);
  }
  35%,
  65% {
    -webkit-transform: translate(-40px, 0px) rotate(-90deg);
    transform: translate(-40px, 0px) rotate(-90deg);
  }
}
@-webkit-keyframes animsquare3 {
  0%,
  15%,
  85%,
  100% {
    -webkit-transform: translate(0px, 0px) rotate(0deg);
    transform: translate(0px, 0px) rotate(0deg);
  }
  40%,
  60% {
    -webkit-transform: translate(-40px, 0px) rotate(-90deg);
    transform: translate(-40px, 0px) rotate(-90deg);
  }
}
@keyframes animsquare3 {
  0%,
  15%,
  85%,
  100% {
    -webkit-transform: translate(0px, 0px) rotate(0deg);
    transform: translate(0px, 0px) rotate(0deg);
  }
  40%,
  60% {
    -webkit-transform: translate(-40px, 0px) rotate(-90deg);
    transform: translate(-40px, 0px) rotate(-90deg);
  }
}
@-webkit-keyframes animsquare4 {
  0%,
  20%,
  80%,
  100% {
    -webkit-transform: translate(0px, 0px) rotate(0deg);
    transform: translate(0px, 0px) rotate(0deg);
  }
  45%,
  55% {
    -webkit-transform: translate(-40px, 0px) rotate(-90deg);
    transform: translate(-40px, 0px) rotate(-90deg);
  }
}
@keyframes animsquare4 {
  0%,
  20%,
  80%,
  100% {
    -webkit-transform: translate(0px, 0px) rotate(0deg);
    transform: translate(0px, 0px) rotate(0deg);
  }
